import { request } from "./request";


//用户注册
export function regsiter(data) {
    return request({
        url: '/api/auth/register',
        method: 'post',
        data

    })
}

//用户登录
export function login(data) {
    return request({
        url: '/api/auth/login',
        method: 'post',
        data

    })
}

//获取用户数据
export function getUser() {
    return request({
        url: '/api/user',
    })
}

//推出登录
export function logout() {
    return request({
        url: '/api/auth/logout',
        method:'post'
    })
}


