<template>
    <top-nav>
        用户登录
    </top-nav>
  <div class="login">
    <van-image
      class="login-img"
      width="200"
      height="150"
      src="https://cdn.jsdelivr.net/npm/@vant/assets/cat.jpeg"
    />
    <van-form @submit="onSubmit">
      <van-cell-group inset>
        <van-field
          v-model="email"
          name="电子邮箱"
          label="电子邮箱"
          placeholder="电子邮箱"
          :rules="[{ required: true, message: '请填写电子邮箱' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
      </van-cell-group>
      <div class="register" @click="goRegister">没有账号！前往注册</div>
      <div style="margin: 16px">
        <van-button
          round
          block
          type="primary"
          native-type="submit"
          class="login-button"
        >
          登录
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import TopNav from '../../components/TopNav.vue'
import { ref, reactive, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import { Notify, Toast } from "vant";
import { login } from "../../network/regsiter";
import {useStore} from 'vuex'
export default {
    name:'Login',
    components: { TopNav },
    setup(){
        const router = useRouter()
        const store = useStore()
        const userInfo = reactive({
            email:'',
            password:''
        })

        //登录
        const onSubmit = () => {
            login(userInfo).then(res => {
                window.localStorage.setItem("token", res.access_token)
                Toast.success("登录成功")
                store.commit("setIsLogin", true)
                setTimeout(() => {
                    router.push({path:'/profile'})
                }, 500)

                userInfo.email = ''
                userInfo.password = ''
            })
        }


        const goRegister = () => {
            router.push({path:'/register'})
        }

        return{
            goRegister,
            ...toRefs(userInfo),
            onSubmit
        }
    }

}
</script>

<style lang="scss" scoped>
.login {
  margin-top: 100px;
  .login-img {
    margin-bottom: 20px;
  }
  .register {
    text-align: left;
    margin-top: 10px;
    margin-left: 15px;
    color: #034e94;
  }
  .login-button {
    background-color: #034e94;
    border: none;
    border-radius: 10px;
  }
}
</style>>